import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/contact',
      name: 'contact',
      component: () => import('@/components/contact/'),
    },
    {
      path: '/buycars',
      name: 'buycars',
      component: () => import('@/components/buycars/'),
    },
    {
      path: '/search',
      name: 'search',
      component: () => import('@/components/search/'),
    },
    {
      path: '/searchInfo',
      name: 'searchInfo',
      component: () => import('@/components/search/info'),
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/components/login/'),
    },
    {
      path: '/forget',
      name: 'forget',
      component: () => import('@/components/forget/member'),
    },
    {
      path: '/forgetAdmin',
      name: 'forgetAdmin',
      component: () => import('@/components/forget/admin'),
    },
    {
      path: '/join',
      name: 'join',
      component: () => import('@/components/join/'),
    },
    {
      path: '/adminLogin',
      name: 'adminLogin',
      component: () => import('@/components/admin/login'),
    },
    {
      path: '/admin',
      name: 'admin',
      component: () => import('@/components/admin/'),
      children: [
        
        {
          path: 'leads',
          name: 'leads',
          component: () => import('@/components/admin/leads/'),
        },
        {
          path: 'memberList',
          name: 'memberList',
          component: () => import('@/components/admin/memberList/'),
        },
        {
          path: 'amidInfo',
          name: 'amidInfo',
          component: () => import('@/components/admin/amidInfo/'),
        },
        {
          path: 'activity',
          name: 'activity',
          component: () => import('@/components/admin/activity/'),
        },
        {
          path: 'activityList',
          name: 'activityList',
          component: () => import('@/components/admin/activityList/'),
        },
        {
          path: 'activityInfo',
          name: 'activityInfo',
          component: () => import('@/components/admin/activityInfo/'),
        },
      ]
    },
    {
      path: '/member',
      name: 'member',
      component: () => import('@/components/member/'),
      children: [
        {
          path: 'info',
          name: 'info',
          component: () => import('@/components/member/info'),
        },
        {
          path: 'favorite',
          name: 'favorite',
          component: () => import('@/components/member/favorite'),
        },
        {
          path: 'message',
          name: 'message',
          component: () => import('@/components/member/message'),
        },
        {
          path: 'unreadMessage',
          name: 'unreadMessage',
          component: () => import('@/components/member/unreadMessage'),
        },
        {
          path: 'postCar',
          name: 'postCar',
          component: () => import('@/components/member/postCar'),
        },
        {
          path: 'favoriteMe',
          name: 'favoriteMe',
          component: () => import('@/components/member/favoriteMe'),
        },
        {
          path: 'browseRecord',
          name: 'browseRecord',
          component: () => import('@/components/member/browseRecord'),
        },
        {
          path: 'lookMe',
          name: 'lookMe',
          component: () => import('@/components/member/lookMe'),
        },
        {
          path: 'certification',
          name: 'certification',
          component: () => import('@/components/member/certification'),
        },
        {
          path: 'myCar',
          name: 'myCar',
          component: () => import('@/components/member/myCar'),
        },
      ]
    },
    
  ]
})



