<template>
    <div class="cartype" :class="searchClass">
        <ul>
            <li class="cartype-info">
                <p class="cartype-title"><span>NEW</span><i class="el-icon-right"/>新車上架</p>
                <div class="cartype-box">
                    <ul :style="`left: ${newListMove}px;`" class="newlist">
                        <li v-for="item in 4" :key="item">
                            
                            <router-link to="/searchInfo">
                            <div class="cartype-img">
                                <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                                <i class="el-icon-search cartype-search"/>
                            </div>
                            </router-link>
                            <div class="cartype-information">
                                <i class="el-icon-time"/>
                                <span>{{newCar.information}}</span>
                            </div>
                            <p class="cartype-name" >{{newCar.name}}</p>
                            <p class="cartype-money" >{{newCar.money}}</p>
                        </li>
                    </ul>
                </div>
                <div class="cartype-arrow">
                    <i class="el-icon-arrow-left" @click="leftMove('newlist')" />
                    <i class="el-icon-arrow-right" @click="rightMove('newlist')"/>
                </div>
            </li>
            <li class="cartype-info">
                <p class="cartype-title"><span>VIEWS</span><i class="el-icon-right"/>熱門瀏覽</p>
                <div class="cartype-box">
                    <ul :style="`left: ${viewsListMove}px;`" class="viewslist">
                        <li v-for="item in 2" :key="item">
                            <router-link to="/searchInfo">
                            <div class="cartype-img">
                                <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                                <i class="el-icon-search cartype-search"/>
                            </div>
                            </router-link>
                            <div class="cartype-information">
                                <i class="el-icon-view"/>
                                <span>100次瀏覽</span>
                            </div>
                            <p class="cartype-name" >{{newCar.name}}</p>
                            <p class="cartype-money" >{{newCar.money}}</p>
                        </li>
                    </ul>
                </div>
                <div class="cartype-arrow">
                    <i class="el-icon-arrow-left" @click="leftMove('viewslist')" />
                    <i class="el-icon-arrow-right" @click="rightMove('viewslist')"/>
                </div>
            </li>
            <li class="cartype-info">
                <p class="cartype-title"><span>Favorites</span><i class="el-icon-right"/>蒐藏排行</p>
                <div class="cartype-box">
                    <ul :style="`left: ${favoritesListMove}px;`" class="favoriteslist">
                        <li v-for="item in 4" :key="item">
                            
                            <router-link to="/searchInfo">
                            <div class="cartype-img">
                                <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                                <i class="el-icon-search cartype-search"/>
                            </div>
                            </router-link>
                            <div class="cartype-information">
                                <i class="el-icon-document-add"/>
                                <span>100次收藏</span>
                            </div>
                            <p class="cartype-name" >{{newCar.name}}</p>
                            <p class="cartype-money" >{{newCar.money}}</p>
                        </li>
                    </ul>
                </div>
                <div class="cartype-arrow">
                    <i class="el-icon-arrow-left" @click="leftMove('favoriteslist')" />
                    <i class="el-icon-arrow-right" @click="rightMove('favoriteslist')"/>
                </div>
            </li>
            <li class="cartype-info">
                <p class="cartype-title"><span>HOT</span><i class="el-icon-right"/>最夯車款</p>
                <div class="cartype-box">
                    <ul :style="`left: ${hotListMove}px;`" class="hotlist">
                        <li v-for="item in 3" :key="item">  
                            <router-link to="/searchInfo">
                            <div class="cartype-img">
                                <img src="~@/assets/img/cartype/photo-1.png" alt="" srcset="">
                                <i class="el-icon-search cartype-search"/>
                            </div>
                            </router-link>
                            <div class="cartype-information">
                                <i class="el-icon-chat-dot-round"/>
                                <span>100次詢問</span>
                            </div>
                            <p class="cartype-name" >{{newCar.name}}</p>
                            <p class="cartype-money" >{{newCar.money}}</p>
                        </li>
                    </ul>
                </div>
                <div class="cartype-arrow">
                    <i class="el-icon-arrow-left" @click="leftMove('hotlist')" />
                    <i class="el-icon-arrow-right" @click="rightMove('hotlist')"/>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'cartype',
    props : {
        searchClass: {
            type:String,
            defalut: "",
        }
    },
    data() {
        return {
            newCar: {
                name: 'Aston Martin Vantage',
                img: '~@/assets/img/cartype/photo-1.png',
                money: '$101,025',
                information: '20天前上架',
            },
            newListMove: 0,
            viewsListMove: 0,
            favoritesListMove: 0,
            hotListMove: 0,
        }
    },
    methods: {
        rightMove(e) {
            const cartype = document.querySelector('.cartype-info').clientWidth
            const newListLength = 0 - document.querySelector('.newlist').clientWidth + cartype
            const viewsListLength = 0 - document.querySelector('.viewslist').clientWidth + cartype
            const favoritesListLength = 0 - document.querySelector('.favoriteslist').clientWidth + cartype
            const hotListLength = 0 - document.querySelector('.hotlist').clientWidth + cartype
            console.log(cartype);
            if ( e === 'newlist' && this.newListMove  > newListLength) { 
                this.newListMove = this.newListMove - cartype
            }else if (e === 'viewslist' && this.viewsListMove  > viewsListLength) {
                this.viewsListMove = this.viewsListMove - cartype
            }else if (e === 'favoriteslist' && this.favoritesListMove  > favoritesListLength) {
                this.favoritesListMove = this.favoritesListMove - cartype
            }else if (e === 'hotlist' && this.hotListMove  > hotListLength) {
                this.hotListMove = this.hotListMove - cartype
            }       
        },
        leftMove(e) {
            const cartype = document.querySelector('.cartype-info').clientWidth
            if ( e === 'newlist' &&  this.newListMove  < 0) {
                this.newListMove = this.newListMove + cartype
            }else if ( e === 'viewslist' && this.viewsListMove  < 0) {
                this.viewsListMove = this.viewsListMove + cartype
            }
            else if ( e === 'favoriteslist' && this.favoritesListMove  < 0) {
                    this.favoritesListMove = this.favoritesListMove + cartype
            }
            else if ( e === 'hotlist' && this.hotListMove  < 0) {
                    this.hotListMove = this.hotListMove + cartype
            }

        },
    }

}
</script>

<style lang='scss' scoped>
.cartype {
    max-width: 1600px;
    margin: 100px auto;
    ul {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        .cartype-info {
            margin: 10px;
        }
    }
    .cartype-box {
        width: 370px;
        overflow: hidden;
        position: relative;
        height: 540px;
        
        ul {
            flex-wrap: nowrap;
            position: absolute;
            left: 0;
            top: 0;
            transition: all .3s;
        }
    }
    
    li {
        .cartype-title {
            font-weight: bold;
            color: #555555;
            display: flex;
            align-items: center;
            span {
                font-size: 40px;
                font-weight: bold;
            }
            i {
                color: #fff;
                background: #999999;
                margin: 0 10px;
            }
        }
        .cartype-img,.cartype-information,.cartype-name,.cartype-money,.cartype-arrow {
            margin: 10px 0 0 10px;
            width: 360px;
        }
        .cartype-img {
            width: 360px;
            height: 400px;
            position: relative;
            cursor: pointer;
            &::after {
                content: " ";
                position: absolute;
                top: -10px;
                left: -10px;
                z-index: -1;
                transition: all .3s;
                border-style: solid;
                border-width: 400px 360px 0 0;
                border-color: #999999 transparent transparent transparent;
            }
            .cartype-search {
                width: 120px;
                height: 120px;
                border-radius: 60px;
                background: rgb(255 103 44 / 70%);
                position: absolute;
                top: calc(50% - 60px);
                left: calc(50% - 60px);
                color: #fff;
                font-size: 50px;
                text-align: center;
                line-height: 120px;
                font-weight: bold;
                opacity: 0;
                transition: all .3s;
            }
            
        }
        .cartype-information {
            background: #999999;
            display: flex;
            width: 360px;
            line-height: 50px;
            color: #fff;
            text-align: center;
            i {
                width: 50px;
                line-height: 50px;
                font-size: 25px;
                background: #555555;
            }
            span {
                display: block;
                width: 100%;
            }
        }
        .cartype-name {
            font-weight: bold;   
        }
        .cartype-money {
            color: #999999;
        }
        .cartype-arrow {
            display: flex;
            justify-content: center;
            margin: 40px 0;
            i {
                width: 40px;
                height: 40px;
                font-size: 35px;
                line-height: 40px;
                text-align: center;
                background: #555555;
                color: #fff;
                cursor: pointer;
                transition: all .3s;
                margin: 0 5px;
                &:hover {
                    background: #ef6d38;
                }
            }
        }
        @media screen and (min-width: 1000px){
            &:hover {
                .cartype-information {
                    background: #ef6d38;
                }
                .cartype-title,.cartype-money { 
                    color: #ef6d38;
                }
                .cartype-img {
                    .cartype-search {
                        opacity: 1;
                    }
                    &::after {
                        border-color: #ef6d38 transparent transparent transparent;
                    }
                }
            }
        }
        
        @media screen and (max-width: 1580px){
            width: 280px;
            overflow: hidden;
            .cartype-img {
                width: 280px;
                height: auto;
                img {
                    width: 100%;
                }
                &::after {
                    border-width: 320px 280px 0 0;
                }
            }
            .cartype-box {
                width: 280px;
                height: 450px;
            }
            .cartype-information{
                width: 280px;
            }
            .cartype-arrow {
                width: 280px;
            }
        }
        @media screen and (max-width: 1365px){
            width: 360px;
            overflow: hidden;
            .cartype-img {
                width: 360px;
                height: auto;
                img {
                    width: 100%;
                }
                &::after {
                    border-width: 400px 360px 0 0;
                }
            }
            .cartype-box {
                width: 360px;
                height: 540px;
            }
            .cartype-information{
                width: 360px;
            }
            .cartype-arrow {
                width: 360px;
            }
        }

    }
    @media screen and (max-width: 1366px){
        max-width: 1250px;
        ul {
        .cartype-info {
            margin: 30px;
        }
    }
    }
    @media screen and (max-width: 768px){
        width: 100%;
        ul {
            .cartype-info {
                margin: auto;
            }
            .cartype-arrow{
            margin: 0 auto 60px;
            }
        }
        .cartype-box {
            height: 550px;
        }
        .cartype-title {
            margin-bottom: 20px;
        }
    }
    @media screen and (max-width: 420px){
        .cartype-info{
            transform: scale(.9);
        }
    }
    
}
.noBorder {
    .cartype-title {
        margin-left: 10px;
    }
    .cartype-img {
        &::after {
            opacity: 0;
        }
    }
}
</style>


