<template>
  <div id="app" :class="{'hideScroll':menu}">
    <Header/>
    <transition name="fade">
    <router-view/>
    </transition>
    <Footer/>
    <i v-if="showSearchButton" class="el-icon-search mobile-btn" @click="openSearchInput"/>
    <div class="search-input" v-if="search && showSearchButton">
        <i class="el-icon-close"  @click="openSearchInput"/>
        <SearchInput />
    </div>
  </div>
</template>

<script>
import Header from './layout/header.vue'
import Footer from './layout/footer.vue'
import SearchInput from './components/search/input.vue'
import { mapState } from 'vuex'

export default {
  components: { Header , Footer, SearchInput },
  name: 'App',
  computed: {
    ...mapState({
        menu: state => state.menu,
        search: state => state.search,
    }),
  },
  data() {
      return {
          showSearchButton: true,
      }
  },
  watch:{
    $route(to){
      const pages = ['home','contact','buycars','search','searchInfo']
      this.showSearchButton = Boolean(pages.find(path=>to.name === path))
    }
  },
  methods:{
    openSearchInput() {
      this.$store.dispatch('switchSearch',!this.search)
    },
  },
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  @media screen and (max-height: 880px) {
    &.hideScroll{
      overflow: hidden !important;
      height: 100vh;
    }
  }
  .mobile-btn {
      display: block;
      background: #464646;
      position: fixed;
      top: 90px;
      color: #fff;
      font-size: 38px;
      padding: 10px 20px;
      right: 20px;
      border-radius: 5px;
      z-index: 2;
      cursor: pointer;
  }
  .search-input {
      background: rgba(0,0,0,.85);
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      left: 0;
      padding: 80px 5%;
      box-sizing: border-box;
      z-index: 100;
      overflow: auto;
      .el-icon-close {
          display: block;
          font-size: 6vmin;
          position: absolute;
          top: 20px;
          right: 20px;
          color: #fff;
          z-index: 101;
          cursor: pointer;
      }
  }
  @media screen and (max-width: 768px){ 
      .mobile-btn {
          transform: scale(.8);
          right: 5px;
      }
      .open {
          display: block !important;
      }
  }
}
</style>



