<script>
import Message from './message'
import Cartype from './cartype'
export default {
  components: { Message, Cartype },
  name: 'home',
  data () {
    return {
      value: 1,
      value2: '',
      value3: '',
      value4: '',
      date1: '',
      date2: '',
      momey: [0,1000],
      bnList: 0,
      bnHeight: '',
      marks: {
          0: '100',
          100: '1000',
          label: this.$createElement('strong', '50%')
      },
      options: [{
          value: '选项1',
          label: 'x'
        },
      ],
      options1: [{
          value: '选项1',
          label: '車1'
        }, 
      ],
      options2: [{
          value: '选项1',
          label: '車1'
        }, 
      ],
      options3: [{
          value: '选项1',
          label: '車1'
        }, 
      ],
      options4: [{
          value: '选项1',
          label: 'x'
        }, 
      ],
      banner: [
        {
          brand: 'Lamborghini Aventador',
          name: 'MODEL 2015',
          money: '$184,900',
          img: 'bn-1.jpg',
        },
        {
          brand: 'Lamborghini Aventador',
          name: 'MODEL 2017',
          money: '$165,050',
          img: 'bn-2.jpg',
        },
        {
          brand: 'Lamborghini Aventador',
          name: 'MODEL 2020',
          money: '$201,050',
          img: 'bn-3.jpg',
        }
      ]
    }
  },
  methods: {
    goSearch() {
      this.$router.push('/search')
    },
    goSearchInfo() {
      this.$router.push('/searchInfo')
    },
    formatTooltip(val) {
        return val * 10;
    },
    bnToggle(current) {
      this.bnList = current
    },
    bnPage(isNext){
      if(isNext){
        this.$refs.carousel.next()
        this.bnList = this.bnList <= this.banner.length-1 ? this.bnList++ : 0
      }else{
        this.$refs.carousel.prev()
        this.bnList = this.bnList >= 0 ? this.bnList-- : this.banner.length-1
      }
    },
    bnResize(){
      window.innerWidth > 1000 ? this.bnHeight = '32.30vw' : this.bnHeight = '54vw'
    }
  },
  created(){
    window.addEventListener('resize', this.bnResize)
    this.bnResize()
  },
  destroyed(){
    window.removeEventListener('resize', this.bnResize);
  }
}
</script>
