import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    menu: false,
    search: false,
    memberSideMenu: false,
    adminSideMenu: false,
}
const getters = {
    menuStatus:()=>{
        return state.menu;
    },
    searchStatus:()=>{
        return state.search;
    },
    memberSideMenuStatus:()=>{
        return state.memberSideMenu;
    },
    adminSideMenuStatus:()=>{
        return state.adminSideMenu;
    },
}

const actions = {
    switchMenu({commit}, status){
        commit('SET_MENU', status);
    },
    switchSearch({commit}, status){
        commit('SET_SEARCH', status);
    },
    switchMemberSideMenu({commit}, status){
        commit('SET_MEMBER_SIDE_MENU', status);
    },
    switchAdminSideMenu({commit}, status){
        commit('SET_ADMIN_SIDE_MENU', status);
    },
}

const mutations = {
    SET_MENU: (state, status) => {
        state.menu = status
      },
    SET_SEARCH: (state, status) => {
    state.search = status
    },
    SET_MEMBER_SIDE_MENU: (state, status) => {
        state.memberSideMenu = status
    },
    SET_ADMIN_SIDE_MENU: (state, status) => {
        state.adminSideMenu = status
    },
}

const module = {}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations,
  module,
})


