<template>
    <div class="head-icon" :style="`width:${width}px; height:${width}px;font-size:${width/1.4}px`">
        <svg v-if="avatar === ''" :style="{ fill: color }" version="1.1" id="avatar" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
            viewBox="0 0 80 80" style="enable-background:new 0 0 80 80;" xml:space="preserve">
            <g id="info">
            </g>
            <g>
                <g id="icons">
                    <g id="user">
                        <ellipse cx="40" cy="29" rx="12.5" ry="15"/>
                        <path d="M64.5,56.7c-2.2-4.5-6.5-8.2-12-10.5c-1.5-0.5-3.2-0.5-4.5,0.2c-2.5,1.5-5,2.2-8,2.2s-5.5-0.7-8-2.2
                            c-1.2-0.5-3-0.7-4.5,0c-5.5,2.2-9.7,6-12,10.5c-1.7,3.2,1,7,4.7,7h39.4C63.5,64,66.2,60.2,64.5,56.7z"/>
                    </g>
                </g>
                <g>
                    <path d="M40,2c21,0,38,17,38,38S61,78,40,78S2,61,2,40S19,2,40,2 M40,0C17.9,0,0,17.9,0,40s17.9,40,40,40s40-17.9,40-40
                        S62.1,0,40,0L40,0z"/>
                </g>
            </g>
        </svg>
        <div v-else class="avatar">
            <img :src="avatar" alt="">
        </div>
    </div>
</template>

<script>
export default {
    name:'headIcon',
    props: {
        width: {
            type: Number,
            default: 60,
        },
        color:{
            type: String,
            default: '#999'
        }
    },
    data(){
        return{
            avatar:'',
        }
    },
}
</script>

<style lang="scss" scoped>
.head-icon {
    border-radius: 100%;
    color: #686868;
    margin-bottom: 10px;
    overflow: hidden;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    #avatar{
        background: #fff;
        border-radius: 100%;
        overflow: hidden;
    }
    .avatar{
        width: 100%;
        height: 100%;
        overflow: hidden;
        border-radius: 100%;
        img{
            max-width: 100%;
            height: auto;
        }
    }
}

</style>


