<template>
    <div class="message">
        <div class="message-box" v-for="item in items" :key="item">
            <div class="message-user">
                <HeadIcon color="#ef6d38" :width="80" />
                <div>
                    <p class="name">William</p>
                    <p class="date">30 MAY 2015 POSTED AT 12:25 AM</p>
                </div>
            </div>
            <div class="message-content">
                <p class="title" @click="openInfo()">Used 2008 to 2014 Mercedes-Benz C-Class</p>
                <div class="text">Curabitur libero. Donec facilisis velit eudsl est. Phasellus consequat. Aenean vita quam. Vivamus et nunc. Nunc consequat sem velde metus imperdiet lacinia. Dui estter neque molestie necd dignissim ac hendrerit quis purus. Etiam sit amet vec convallis massa scelerisque mattis. Sed placerat leo nec.</div>
            </div>
            <i class="el-icon-right" />
        </div>
    </div>
</template>

<script>
import HeadIcon from '../../layout/headIcon.vue'
export default {
    name: 'message',
    props: {
        items: {
            type:Number,
            defalut: 0,
        }
        
    },
    components:{
        HeadIcon,
    },

    data () {
        return {

        }
    },
    methods: {
        openInfo() {
            this.$emit('openInfo', true)
            window.scrollTo(0, 0)
        },
    },
}
</script>

<style lang='scss' scoped>
.message {
    max-width: 1200px;
    margin: auto;
    padding: 40px 0;
    width: 90%;
    .message-box {
        padding: 40px;
        position: relative;
        cursor: pointer;
        &:nth-child(even) {
            background: #f4f4f4;
        }
        &:hover {
            .el-icon-right {
                background:#ef6d38;
            }
        }
        .message-user {
            display: flex;
            align-items: center;
            .head-icon {
                color: #ef6d38;
                margin: 0;
                margin-right: 20px;
            }
            .name {
                color: #ef6d38;
                font-size: 22px;
                margin-bottom: 5px;
            }
            .date {
                color: #999999;
            }
        }
        .message-content {
            max-width: 950px;
            margin-left: 107px;
            width: 80%;
            .title {
                font-size: 22px;
                text-decoration: underline;
                margin-bottom: 20px;
            }
            .text {
                color: #333333;
            }
        }
        .el-icon-right {
            width: 50px;
            height: 50px;
            color: #fff;
            background: #555555;
            font-size: 40px;
            text-align: center;
            box-sizing: border-box;
            padding-top: 5px;
            position: absolute;
            right: 0;
            transition: all .3s;
            top: calc( 50% - 25px );
        }
    }
    @media screen and (max-width: 768px){
        .el-icon-right {
            display: none;
        }
        .message-box .message-user .el-icon-user-solid {
            width: 40px;
            height: 40px;
            font-size: 40px;
        }
        .message-box .message-content {
            margin-left: 0;
            width: 100%;
            margin-top: 15px;
        }
        
    }
}
</style>


