<template>
    <el-header class="header">
        <router-link to="/" class="logo">
            <img src="~@/assets/img/logo.jpg" alt="" srcset="">
        </router-link>
        <i v-if="page.includes('member')" class="el-icon-message-solid mesBtn" @click="getMes"><span>99</span> </i>
        <div class="icon-menu" v-if="!menu" @click="openMenu"><span ></span></div>
        <div class="icon-menu" v-else @click="openMenu"><i class="el-icon-close"></i></div>
        <div class="menu-content" :class="{'closeMenu': !menu}">
            <nav>
                <a @click="goPage('/')"><p class="title">返回首頁<span class="sub-title">HOME</span></p><i class="el-icon-arrow-right"/></a> 
                <a @click="goPage('/login')"><p class="title">會員專區<span class="sub-title">MEMBER CENTER</span></p><i class="el-icon-arrow-right"/></a> 
                <a @click="goPage('/search')"><p class="title">我要買車<span class="sub-title">BUY CAR</span></p><i class="el-icon-arrow-right"/></a> 
                <a @click="goPage('/adminLogin')"><p class="title">我要賣車<span class="sub-title">SELL CAR</span></p><i class="el-icon-arrow-right"/></a> 
                <a @click="goPage('/buycars')"><p class="title">車輛討論/詢問<span class="sub-title">MEMBER CENTER</span></p><i class="el-icon-arrow-right"/></a> 
                <a @click="goPage('/contact')"><p class="title">聯絡我們<span class="sub-title">CONTACT</span></p><i class="el-icon-arrow-right"/></a> 
            </nav>
        </div>
    </el-header>
</template>

<script>
import { mapState } from 'vuex'

export default {
    name: 'Header',
    computed: {
        ...mapState({
            menu: state => state.menu,
        }),
        page() {
            return this.$route.path
        },
    },
    methods: {
        openMenu() {
            this.$store.dispatch('switchMenu',!this.menu)
        },
        getMes() {
            this.$router.push('/member/unreadMessage')
        },
        goPage(path){
            this.$router.currentRoute.path !== path && this.$router.push(path)
            this.$store.dispatch('switchMenu',false)
        }
    }

}
</script>

<style lang="scss" scoped>
.header{ 
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 0;
    z-index: 100;
    width: 100vw;
    .logo {
        width: 200px;
        display: block;
        cursor: pointer;
        img{
            width: 100%;
            height: auto;
            max-height: 100%;
        }
    }
    .mesBtn {
        color: #555555;
        font-size: 50px;
        position: fixed;
        cursor: pointer;
        right: 35px;
        top: 90px;
        span {
            background: #ef6d38;
            color: #fff;
            position: absolute;
            font-size: 10px;
            padding: 5px;
            right: -8px;
            border-radius: 20px;
        }
    }
    .icon-menu {
        position: fixed;
        top: 15px;
        right: 20px;
        text-align: center;
        width: 78px;
        height: 60px;
        background: #ef6d38;
        border-radius: 5px;
        box-sizing: border-box;
        cursor: pointer;
        transition: all .5s;
        z-index: 1;
        span {
            display: inline-flex;
            width: 50px;
            height: 4px;
            background: #fff;
            margin-top: 28px;
            &::after , &::before{
                content: '';
                display: block;
                position: absolute;
                width: 50px;
                height: 4px;
                background: #fff;
            }
            &::after {
                top: 10px;
            }
            &::before {
                bottom: 10px;
            }
        }
        .el-icon-close {
            &:before {
                font-size: 60px;
                color: #fff;
            }
        }
        &:hover {
            background: #d34b15;
        }
    }
    
    .menu-content {
        position: fixed;
        top: 0;
        right: 0; 
        background: #555555;
        width: 600px; 
        height: 100vh;
        overflow-y: auto;
        transition: all .5s;
        opacity: 1;
        nav{
            border-top: 1px #666 solid;
            margin: 105px 0;
            padding: 30px 0;
            >a {
                height: 100px;
                display: flex;
                justify-content: space-between;
                padding: 0 95px;
                align-items: center;
                transition: all .3s;
                cursor: pointer;
                .title {
                    font-size: 24px;
                    color: #fff;
                }
                .sub-title {
                    font-size: 14px;
                    color: #9999;
                    display: block;
                    transition: all .3s;
                }
                i {
                    color: #fff;
                    font-size: 24px;
                    transition: all .3s;
                }
                &:hover {
                    background: #5f5f5f;
                    .sub-title {
                        color: #fff;
                    }
                    i {
                        color: #db6b3d;
                    }
                }
            }
        }
        @media screen and (max-width: 768px){  
            width: 100%;
        }
    }
    .closeMenu {
        right: -600px;
        opacity: 0.3;
        @media screen and (max-width: 768px){  
            right: -100%;
        }
    }
    @media screen and (max-width: 1000px){ 
        .logo {
            width: auto;
            height: 60px;
            margin-top: -10px;
        }
    }
    @media screen and (max-width: 768px){ 
        padding-left: 0;
        .icon-menu{ 
            transform: scale(0.8);
            right: 5px;
        }
        .logo {
            width: auto;
            height: 48px;
            margin-left: 15px;
        }
        .mesBtn{
            right: 20px;
        }
    }
    
}    

</style>


